import { unauthorized } from "./errors.ts";

const CF_ACCESS_USER_EMAIL = "CF-Access-Authenticated-User-Email";
/** Only used when CF header is missing (e.g. local dev). Not sent by browsers; use for testing. */
const DEV_USER_EMAIL = "X-Dev-User-Email";

/**
 * Reads the user email from the request. Throws if header is missing.
 * Production: use CF-Access-Authenticated-User-Email (set by Cloudflare Access).
 * Local dev: use X-Dev-User-Email when CF header is absent so you can test without Access.
 */
export function getUserEmail(request: Request): string {
  const email =
    request.headers.get(CF_ACCESS_USER_EMAIL)?.trim() ||
    request.headers.get(DEV_USER_EMAIL)?.trim();
  if (!email) {
    throw unauthorized(
      "Access required. Ensure Cloudflare Access is enabled (production) or send X-Dev-User-Email (local dev)."
    );
  }
  return email;
}

/**
 * Derives a display name from email: part before @, replace dots/underscores with spaces, capitalize words.
 */
export function displayNameFromEmail(email: string): string {
  const local = email.split("@")[0] ?? email;
  const words = local.replace(/[._]/g, " ").split(/\s+/).filter(Boolean);
  return words
    .map((w) => w.charAt(0).toUpperCase() + w.slice(1).toLowerCase())
    .join(" ")
    .trim() || local;
}
