/** Room code: 4–5 uppercase chars, no ambiguous (I,O,0,1). */
export type RoomCode = string;

/** User email from CF-Access-Authenticated-User-Email. */
export type Email = string;

export type Timestamp = number;

export interface Mission {
  id: string;
  title: string;
  description?: string;
  points: number;
  difficulty: "easy" | "medium" | "hard" | "legendary";
  tags?: string[];
}

export interface AssignedMission {
  missionId: string;
  completed: boolean;
  completedAt?: Timestamp;
}

export interface Player {
  email: Email;
  displayName: string;
  /** Score is derived from completed missions; do not set directly. */
  score: number;
  joinedAt: Timestamp;
  assignedMissions: AssignedMission[];
}

export type EventType =
  | "joined"
  | "left"
  | "claimed"
  | "closed"
  | "mission_completed"
  | "mission_uncompleted";

export interface RoomEvent {
  at: Timestamp;
  type: EventType;
  by: Email;
  meta?: { points?: number; note?: string; missionId?: string };
}

export interface Room {
  code: RoomCode;
  name: string;
  createdAt: Timestamp;
  creatorEmail: Email;
  isClosed: boolean;
  commonMissionId: string;
  missionDeckVersion?: string;
  players: Record<Email, Player>;
  events: RoomEvent[];
}

export const MAX_EVENTS = 50;
